let resolved = false
const classes = {
  box: 'f_rvcbox',
  def: 'f_vcrdefault',
  spons: 'f_sponsorship'
}

function scan (article) {
  return new Promise(function scanner(resolve, reject) {
    const targetClass = 'is-movable'

    const articleRelatedGroup = document.querySelector('.f_rvcbox')
    if (!articleRelatedGroup) {
      setTimeout(scanner.bind(null, resolve, reject), 500)
      reject('no vcr found in content')
    }
    const bottomRVC = document.querySelector('.article__main > .related__group[data-cols="2"]')
    if (!bottomRVC) {
      setTimeout(scanner.bind(null, resolve, reject), 500)
      reject('no bottom rvc boxes')
    } else {
      const movableBox = bottomRVC.querySelector(`.${targetClass}`)
      if (!movableBox) {
        setTimeout(scanner.bind(null, resolve, reject), 500)
        reject('no movable box found')
      }

      if (articleRelatedGroup && bottomRVC && movableBox) {
        resolve({ articleRelatedGroup, bottomRVC })
      }
    }
  })
}

export default {
  init (config, { event }) {
    const article = document.querySelector('article.article')
    if (!article) return

    const { type } = article.dataset
    if (!['article', 'podcast'].includes(type)) return

    let count = 0
    let bRVC, rvcList, aRG

    const moveVCRtoBottom = ({ articleRelatedGroup, bottomRVC }) => {
      bRVC = bottomRVC
      aRG = articleRelatedGroup

      if (window.innerWidth >= 768) {
        event.trigger('dynamicVCRBoxMoveDone')
      }

      setTimeout(() => {
        articleRelatedGroup.classList.add('aRG')
        const articleRelatedGroupChildren = document.querySelectorAll('.f_rvcbox > div, .f_rvcbox > ul')
        for (const child of articleRelatedGroupChildren) {
          const li = document.createElement('li')
          const clone = child.cloneNode(true)
          if (clone.nodeName === 'UL') {
            rvcList = clone
            if (window.innerWidth < 768) {
              // reworkVCRBoxClasses(clone)
              clone.dataset.place = 'EMBEDDED'
            }
          } else {
            if (window.innerWidth >= 768) {
              clone.classList.remove(classes.spons)
            }
          }

          li.classList.add('moved')
          li.append(clone)
          // console.log('brvc', bottomRVC)
          bottomRVC.append(li)
          count += 1
        }

        if (window.innerWidth < 768) {
          bottomRVC.dataset.cols = count + 1
          rvcList.dataset.place = 'EMBEDDED'
          reworkVCRBoxClasses(rvcList, aRG)
          event.trigger('dynamicVCRBoxMoveDone')
        } else {
          reworkVCRBoxClasses(aRG, rvcList)
        }
        article.classList.add('moved-vcr')
        resolved = true
      }, 500);
    }

    if (!resolved) scan(article).then(moveVCRtoBottom).catch(e => {
      // console.error(e)
      event.trigger('dynamicVCRBoxMoveDone')
    })

    // readjust cols count on resize
    window.addEventListener('resize', () => {
      if (bRVC) {
        bRVC.dataset.cols = window.innerWidth < 768 ? count + 1 : 2
      }
      if (rvcList) {
        if (window.innerWidth < 768) {
          reworkVCRBoxClasses(rvcList, aRG)
        } else {
          reworkVCRBoxClasses(aRG, rvcList)
        }
      }
    })
  }
}

const reworkVCRBoxClasses = (el, originalEl) => {
  const { box, def, spons } = classes

  el.classList.add(box)
  el.dataset.place = 'EMBEDDED'
  // console.log('el', el, 'originalEl', originalEl)
  const lis = el.querySelectorAll(`li`)
  const { parentNode: { parentNode } } = el
  for (let e of lis) {
    e.classList.add(def)
  }
  if (parentNode) {
    const sponsr = parentNode.querySelector(`.${spons}`)
    if (sponsr) {
      sponsr.classList.add(spons)
    } else {
      console.log('no sponsorship found')
    }
  }

  if (originalEl) {
    originalEl.classList.remove(box)
    const olis = originalEl.querySelectorAll(`li`)
    for (let e of olis) {
      e.classList.remove(def)
    }
    const origS = originalEl.parentNode.querySelector(`.${spons}`)
    if (origS) origS.classList.remove(spons)
  }
}
